.academy__header {
    display: flex;
    flex-direction: row;
}

.academy__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: 3rem;
}

.academy__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 65px;
    align-content: center;
    text-align: center;
    letter-spacing: -0.04em;
}

.academy__header-content p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 17.5px;
    line-height: 25px;
    align-content: center;
    text-align: center;
    color: var(--color-text);
    margin-top: 1.5rem;
    margin-left: 6rem;
    margin-right: 6rem;
}

.button-container {
    display: flex;
    justify-content: start;
    align-items: start;
    padding: 20px 0px;
}
  
.sign-in-button {
    padding: 15.5px 35px;
    font-family: var(--font-family);
    font-size: 15.5px;
    color: #FFFFFF;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 1rem;
    transition: background-color 0.3s ease;
    background: #e92424;
    filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.20));
}

.register-button {
    padding: 15.5px 35px;
    font-size: 15.5px;
    font-family: var(--font-family);
    font-weight: 500;
    margin-left: 0.65rem;
    color: #000000;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 1rem;
    transition: background-color 0.3s ease;
    background: #f7cfcf;
}

.academy__header-image {
    flex: 1;
    display: flex;
    justify-content: space-around;
    border: none;
    border-radius: 30px;
    flex-direction: row;
    max-width: 80%;
    margin-top: 40px;
}

.academy__header-image img {
    border-radius: 20px;
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .academy__header {
        flex-direction: column;
    }

    .academy__header-content p {
        font-size: 14px;
        margin: 0rem;
        margin-top: 1rem;
        line-height: 24px;
    }

    .academy__header-content {
        margin: 0 0 0rem;
    }
}

@media screen and (max-width: 650px) {
    .academy__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .academy__header-content {
        margin: 0 0 0rem;
    }

    .academy__header-content p {
        font-size: 14px;
        margin: 0rem;
        margin-top: 1rem;
        line-height: 24px;
    }

    .button-container {
        flex-direction: column;
        align-items: start;
        justify-content: start;
    }
    
    .sign-in-button,
    .register-button-2 {
        width: 100%;
        margin-bottom: 10px;
        margin: 0px;
        align-items: start;
        margin-left: 0.00rem;
    }

    .academy__header-image {
        flex-direction: column;
        max-width: 100%;
        margin-top: 20px;
    }

    .academy__header-image img {
        border-radius: 10px;
    }
}

@media screen and (max-width: 490px) {
    .academy__header-content {
        align-items: start;
        align-content: start;
    }
    
    .academy__header-content h1 {
        font-size: 31px;
        line-height: 40px;
        text-align: start;
    }

    .academy__header-content p {
        font-size: 14px;
        margin: 0rem;
        margin-top: 1rem;
        text-align: start;
        line-height: 24px;
    }

    .button-container {
        align-items: start;
        justify-content: start;
        flex-direction: column;
    }

    .register-button-2 {
        background-color: #e92424;
        color: #FFFFFF;
        align-items: start;
        align-content: start;
        margin-top: 0.5rem;
        margin-left: 0px;
    }

    .academy__header-image {
        flex-direction: column;
        max-width: 100%;
        margin-top: 20px;
    }

    .academy__header-image img {
        border-radius: 10px;
    }
}