.academy__whatAcademy {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    border-radius: 15px;
    background: var(--color-footer);

    background:-moz-radial-gradient(circle at 30% -100%, #eed7dd 25%, rgb(248, 187, 190) 85%, rgb(235, 101, 108) 100%);
    background:-webkit-radial-gradient(circle at 30% -100%, #eed7dd 25%, rgb(248, 187, 190) 85%, rgb(235, 101, 108) 100%);
    background:-o-radial-gradient(circle at 30% -100%, #eed7dd 25%, rgb(248, 187, 190) 85%, rgb(235, 101, 108) 100%);
    background:-ms-radial-gradient(circle at 30% -100%, #eed7dd 25%, rgb(248, 187, 190) 85%, rgb(235, 101, 108) 100%);
    background:radial-gradient(circle at 30% -100%, #eed7dd 25%, rgb(248, 187, 190) 85%, rgb(235, 101, 108) 100%);
    
    filter: drop-shadow(0px 3px 3px rgba(66, 66, 66, 0.25));
}

.academy__whatAcademy-feature {
    display: flex;
}

.academy__whatAcademy-feature .academy__features-container__feature {
    margin: 0;
}

.academy__whatAcademy-feature .academy__features-container_feature-text {
    max-width: 700px;
}

.academy__whatAcademy-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.academy__whatAcademy-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    max-width: 510px;
}

.academy__whatAcademy-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: red;
}

.academy__whatAcademy-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.academy__whatAcademy-container .academy__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.academy__whatAcademy-container .academy__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .academy__whatAcademy-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .academy__whatAcademy-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    .academy__whatAcademy-feature .academy__features-container__feature {
        flex-direction: column;
    }

    .academy__whatAcademy-feature .academy__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .academy__whatAcademy-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    .academy__whatAcademy-container .academy__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}