.academy__blog-container_article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    background: rgb(230, 229, 229);
}

.academy__blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}

.academy__blog-container_article-image img {
    width: 100%;
    height: 100%;
    border-radius: 15px 15px 0px 0px;
}

.academy__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.academy__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 12.65px;
    font-weight: 700;
    line-height: 35px;
    color: #ff0000;
}

.academy__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.30px;
    color: #464646;
    margin-bottom: 5rem;
    cursor: pointer;
}

.academy__blog-container_article-content p:last-child {
    cursor: pointer;
}


@media screen and (max-width: 550px) {
    .academy__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}